export function formatNumberToThousandSeparator(number) {
    // 处理负数
    const isNegative = number < 0;
    const absoluteNumber = Math.abs(number);

    // 整数部分和小数部分分离
    let [integer, decimal] = absoluteNumber.toString().split('.');

    // 格式化整数部分
    integer = integer.replace(/\B(?=(\d{3})+(?!\d))/g, ',');

    // 如果有小数部分，保留并拼接
    if (decimal) {
        return (isNegative ? '-' : '') + integer + '.' + decimal;
    } else {
        return (isNegative ? '-' : '') + integer;
    }
}