<script>
import * as echarts from "echarts/core";
export default {
  name: "RightChart1",
  data() {
    return {};
  },
  mounted() {
    // this.initChart(echarts.init(this.$refs.headChart1), 80, "CSD");
    // this.initChart(echarts.init(this.$refs.headChart2), 50, "NCB");
    this.setProgress(document.querySelectorAll(".circleRight1")[0], 23.6);
    this.setProgress(document.querySelectorAll(".circleRight1")[1], 52.8);
    this.setProgress(document.querySelectorAll(".circleRight1")[2], 33.3);
    this.setProgress(document.querySelectorAll(".circleRight1")[3], 45.6);
  },
  methods: {
    setProgress(element, progress) {
      const circle = element.querySelector(".progress");
      const radius = circle.r.baseVal.value;
      const circumference = 2 * Math.PI * radius;
      const offset = circumference - (progress / 100) * circumference;
      circle.style.strokeDashoffset = offset;
    },
    initChart(myChart, value, text) {
      var colorList = ["rgb(233, 54, 112)", "rgb(12, 26, 82)"];
      const option = {
        title: {
          text: value + "%",
          x: "30%",
          y: "center",

          textStyle: {
            fontSize: 14,
            color: "rgba(255, 255, 255, 0.8)",
          },
        },
        tooltip: {
          trigger: "item",
          show: false,
        },
        graphic: {
          elements: [
            {
              type: "text",
              left: "15%",
              top: "85%", // 根据需要调整文本位置
              style: {
                text: text + "额成长率",
                textAlign: "center",
                font: "14px Arial",
                fill: "rgba(255, 255, 255, 0.8)",
              },
            },
          ],
        },
        series: [
          {
            type: "pie",
            center: ["45%", "50%"],
            radius: ["50%", "75%"],
            clockwise: false,
            avoidLabelOverlap: false,
            hoverOffset: 0,
            itemStyle: {
              normal: {
                color: function (params) {
                  return colorList[params.dataIndex];
                },
              },
            },
            label: {
              show: false,
            },
            labelLine: {
              normal: {
                length: 20,
                length2: 30,
                lineStyle: {
                  width: 1,
                },
              },
            },
            data: [
              {
                name: "已完成",
                value: value,
              },
              {
                name: "未完成",
                value: 100 - value,
              },
            ],
          },
        ],
      };
      myChart.setOption(option);
    },
  },
};
</script>

