<style scoped lang="less">
.main-center {
  width: 100%;
  height: 100%;
  display: flex;
  justify-content: space-between;
}
.center-left,
.center-right {
  width: 49.6%;
  height: 98.2%;
  background-color: rgba(75, 35, 159, 0.5); /* 半透明背景 */
  backdrop-filter: blur(10px); /* 背景模糊 */
  -webkit-backdrop-filter: blur(10px); /* 兼容老版本 Safari */
  .heard-title {
    height: 30px;
    font-size: 24px;
    width: 100%;
    text-align: center;
    margin-top: 10px;
  }
  /*最外层透明*/
  ::v-deep .el-table,
  ::v-deep .el-table__expanded-cell {
    background-color: transparent;
  }
  /* 表格内背景颜色 */
  ::v-deep .el-table th,
  ::v-deep .el-table tr,
  ::v-deep .el-table td {
    background-color: transparent;
    color: white;
  }

  // 鼠标经过颜色改为粉红色
  ::v-deep .el-table__body tr:hover > td {
    background-color: rgba(230, 230, 250, 0.3) !important;
  }
  /* 删除表格下最底层的横线 */
  ::v-deep .el-table::before {
    left: 0;
    bottom: 0;
    width: 100%;
    height: 0px;
  }

  .circle-container {
    display: flex;
    align-items: center;
    height: 30px;
    .circleColleRate {
      width: 30px;
      margin: 10px 0 0 5px;
    }
    .circleColleRate svg {
      transform: rotate(-90deg); /* 使进度从顶部开始 */
    }

    .circleColleRate circle {
      fill: none;
      stroke-width: 3; /* 调整圆环宽度 */
    }

    .circleColleRate .bg {
      stroke: #e0e0e0; /* 背景圆环颜色 */
    }

    .circleColleRate .progress {
      stroke: rgb(233, 54, 112); /* 进度条颜色 */
      stroke-dasharray: 52; /* 总长度：2 * π * 半径（15px） */
      stroke-dashoffset: 10; /* 初始偏移量为总长度 - 进度长度 */
      transition: stroke-dashoffset 1s;
    }

    .circlekaidanRate {
      width: 30px;
      margin: 10px 0 0 5px;
    }
    .circlekaidanRate svg {
      transform: rotate(-90deg); /* 使进度从顶部开始 */
    }

    .circlekaidanRate circle {
      fill: none;
      stroke-width: 3; /* 调整圆环宽度 */
    }

    .circlekaidanRate .bg {
      stroke: #e0e0e0; /* 背景圆环颜色 */
    }

    .circlekaidanRate .progress {
      stroke: rgb(233, 54, 112); /* 进度条颜色 */
      stroke-dasharray: 45; /* 总长度：2 * π * 半径（15px） */
      stroke-dashoffset: 15; /* 初始偏移量为总长度 - 进度长度 */
      transition: stroke-dashoffset 1s;
    }
  }
}
</style>
