<script>
export default {
  name: "LeftChart3",
  data() {
    return {};
  },
  mounted() {
    this.setProgress(document.querySelectorAll(".circle3")[0], 23.6);
    this.setProgress(document.querySelectorAll(".circle3")[1], 52.8);
    this.setProgress(document.querySelectorAll(".circle3")[2], 33.3);
    this.setProgress(document.querySelectorAll(".circle3")[3], 55.5);
    this.setProgress(document.querySelectorAll(".circle3")[4], 76.2);
    this.setProgress(document.querySelectorAll(".circle3")[5], 90.5);
  },
  methods: {
    setProgress(element, progress) {
      const circle = element.querySelector(".progress");
      const radius = circle.r.baseVal.value;
      const circumference = 2 * Math.PI * radius;
      const offset = circumference - (progress / 100) * circumference;
      circle.style.strokeDashoffset = offset;
    },
  },
};
</script>

