<template>
  <div id="tongluliContent">
    <div class="content-heard">
      <div class="customers-total">
        <div class="customers-total-left">
          <i class="el-icon-s-custom"></i>
        </div>
        <div class="customers-total-right">
          <h3>{{ typeTitle[type] }}总数</h3>
          <h1>{{ typeValue[type] }}</h1>
        </div>
      </div>
      <div
        v-for="(item, index) in headerData"
        :key="index"
        class="customers-info"
      >
        <div class="info-item" :style="{ borderRight: item.border }">
          <h3>{{ item.title }}</h3>
          <h1>{{ item.typeValue[type] }}</h1>
        </div>
      </div>
    </div>
    <div class="content-center">
      <div class="left-chart">
        <div style="width: 100%; height: 100%" ref="leftChart"></div>
      </div>
      <div class="right-chart">
        <div style="width: 100%; height: 100%" ref="rightChart"></div>
      </div>
    </div>
    <div class="content-bottom"></div>
  </div>
</template>
<script>
import * as echarts from "echarts/core";

export default {
  name: "TongluliContent",
  props: {
    type: {
      type: String,
      default: 1,
    },
  },
  components: {},
  mounted() {
    this.initLeftChart(echarts.init(this.$refs.leftChart));
  },
  data() {
    return {
      typeTitle: {
        1: "三阶客户",
        2: "直营二阶客户",
        3: "非直营二阶客户",
        4: "餐饮批/镇分销客户",
      },
      typeValue: {
        1: 5000,
        2: 4000,
        3: 3000,
        4: 2000,
      },
      headerData: [
        {
          title: "增长数",
          typeValue: { 1: 500, 2: 400, 3: 200, 4: 100 },
          border: "1px solid #D3D3D3	",
        },
        {
          title: "成交率",
          typeValue: { 1: "50%", 2: "40%", 3: "30%", 4: "20%" },
          border: "1px solid #D3D3D3	",
        },
        {
          title: "业绩成长率MTD",
          typeValue: { 1: "55%", 2: "45%", 3: "35%", 4: "25%" },
          border: "1px solid #D3D3D3	",
        },
        {
          title: "业绩成长率YTD",
          typeValue: { 1: "58%", 2: "48%", 3: "38%", 4: "28%" },
          border: "none",
        },
      ],
    };
  },
  methods: {
    initLeftChart(myChart) {
      // myChart.style.width = window.innerWidth + "px";
      // myChart.style.height = window.innerHeight + "px";
      const option = {
        tooltip: {
          trigger: "item",
          axisPointer: {
            // 坐标轴指示器，坐标轴触发有效
            type: "shadow", // 默认为直线，可选为：'line' | 'shadow'
          },
        },
        grid: {
          left: "2%",
          right: "4%",
          bottom: "14%",
          top: "16%",
          containLabel: true,
        },
        legend: {
          data: ["关停总数", "复机数", "复机率"],
          left: "7%",
          top: "5%",
          textStyle: {
            color: "#666666",
          },
          itemWidth: 15,
          itemHeight: 10,
          itemGap: 25,
        },
        xAxis: {
          type: "category",
          data: [
            "TZ21",
            "TZ22",
            "TZ24",
            "TZ25",
            "TZ26",
            "TZ27",
            "TZ28",
            "TZ29",
            "TZ30",
            "TZ31",
            "TZ32",
            "TZ33",
            "SZX开头",
            "其他TZ开头",
          ],
          axisLine: {
            lineStyle: {
              color: "#cdd5e2",
            },
          },
          axisLabel: {
            interval: 0,
            textStyle: {
              color: "#666666",
            },
          },
        },

        yAxis: [
          {
            type: "log",
            min: 1,
            logBase: 3,
            axisLine: {
              show: false,
              lineStyle: {
                color: "#cdd5e2",
              },
            },
            splitLine: {
              show: false,
            },
            axisLabel: {
              textStyle: {
                color: "#666666",
              },
            },
            axisLine: {
              lineStyle: {
                color: "#cdd5e2",
              },
            },
          },
          {
            type: "value",
            name: "百分比",
            nameTextStyle: {
              color: "#666666",
            },
            position: "right",
            axisLine: {
              lineStyle: {
                color: "#cdd5e2",
              },
            },
            splitLine: {
              show: false,
            },
            axisLabel: {
              show: true,
              formatter: "{value} %", //右侧Y轴文字显示
              textStyle: {
                color: "#666666",
              },
            },
          },
        ],
        series: [
          {
            name: "关停总数",
            type: "bar",
            barWidth: "12px",
            itemStyle: {
              normal: {
                color: new echarts.graphic.LinearGradient(0, 0, 0, 1, [
                  {
                    offset: 0,
                    color: "#29acff",
                  },
                  {
                    offset: 1,
                    color: "#4bdfff",
                  },
                ]),
                barBorderRadius: 6,
              },
            },
            data: [11, 14, 133, 4, 0, 14, 116, 12, 12, 58, 15, 12, 69, 2],
          },
          {
            name: "复机数",
            type: "bar",
            barWidth: "12px",
            itemStyle: {
              normal: {
                color: new echarts.graphic.LinearGradient(0, 0, 0, 1, [
                  {
                    offset: 0,
                    color: "#01c871",
                  },
                  {
                    offset: 1,
                    color: "#55f49c",
                  },
                ]),
                barBorderRadius: 6,
              },
            },
            data: [0, 1, 9, 0, 0, 3, 0, 2, 0, 0, 1, 0, 39, 0],
          },
          {
            name: "复机率",
            type: "line",
            yAxisIndex: 1, //使用的 y 轴的 index，在单个图表实例中存在多个 y轴的时候有用
            smooth: false, //平滑曲线显示

            symbol: "circle", //标记的图形为实心圆
            symbolSize: 8, //标记的大小
            itemStyle: {
              normal: {
                color: "#ffa43a",
                borderColor: "rgba(255, 234, 0, 0.5)", //圆点透明 边框
                borderWidth: 5,
              },
            },
            lineStyle: {
              color: "#ffa43a",
            },

            data: [
              0.0, 7.14, 6.77, 0.0, 0.0, 21.43, 0.0, 16.67, 0.0, 0.0, 6.67, 0.0,
              56.52, 0.0,
            ],
          },
        ],
      };
      myChart.setOption(option);
      myChart.resize();
    },
  },
};
</script>
<style scoped lang="less">
#tongluliContent {
  .content-heard {
    width: 100%;
    height: 200px;
    display: flex;
    // justify-content: center;
    .customers-total {
      display: flex;
      justify-content: center;
      background-color: rgb(216, 228, 252);
      width: 20%;
      .customers-total-left {
        width: 50%;
        height: 100%;
        display: flex;
        justify-content: center;
        align-items: center;
        ::v-deep .el-icon-s-custom {
          color: rgb(46, 116, 255);
          font-size: 60px;
        }
      }
      .customers-total-right {
        width: 50%;
        height: 100%;
        display: flex;
        flex-direction: column;
        justify-content: center;
      }
    }
    .customers-info {
      width: 20%;
      height: 100%;
      position: relative;
      .info-item {
        width: 100%;
        height: 70%;
        text-align: center;
        position: absolute;
        top: 50%;
        left: 50%;
        transform: translate(-50%, -50%);
        h3 {
          margin-bottom: 40px;
        }
        h1 {
          margin-top: 40px;
        }
      }
      .notBorder {
        border: none !important;
      }
    }
  }
  .content-center {
    width: 100%;
    height: 500px;
    display: flex;
    .left-chart,
    .right-chart {
      width: 48%;
      margin: 0 auto;
      height: 100%;
    }
  }
}
</style>
