  computed: {
    // 对数据按 value 进行排序，排序号从 1 开始
    sorteBudgetData() {
      return this.budgetData
        .sort((a, b) => a.value - b.value)
        .map((item, index) => ({
          ...item,
        }));
    },
    sortMapRightData() {
      return this.mapRightData
        .sort((a, b) => b.value - a.value)
        .map((item, index) => ({
          ...item,
          sort: index + 1,
        }));
    },
    sortRightData1() {
      return this.rightData1.sort((a, b) => b - a);
    },
    sortRightData2() {
      return this.rightData2.sort((a, b) => b - a);
    },
  },
  methods: {
    initLeftCharts(myChart) {
      let width = document.getElementById("budgetCharts").clientWidth;
      let height = document.getElementById("budgetCharts").clientHeight;
      const option = {
        tooltip: {
          trigger: "axis",
          axisPointer: {
            type: "shadow",
          },
        },
        grid: {
          left: "35%", // 左边距
          right: "20%", // 右边距
          top: "5%", // 上边距
          bottom: "1%", // 下边距
        },
        xAxis: {
          type: "value",
          show: false, // 不显示横坐标轴线,
          position: "top",
        },
        yAxis: {
          type: "category",
          data: this.sorteBudgetData.map(
            (item, index) =>
              `${this.sorteBudgetData.length - index}. ${item.name}`
          ), // 左侧显示排序号、name 和 value
          axisLine: {
            show: false, // 不显示纵坐标轴线
          },
          axisTick: {
            show: false, // 不显示纵坐标轴刻度线
          },
        },
        series: [
          {
            name: "Value",
            type: "bar",
            data: this.sorteBudgetData.map((item) => item.value),
            itemStyle: {
              color: "#58b5c0", // 设置柱子的颜色
            },
            label: {
              show: true,
              position: "insideRight", // 标签显示在柱子的右侧
              formatter: "{c}", // 显示数值
              color: "#fff", // 标签文字颜色
            },
          },
        ],
      };
      myChart.resize({
        width: width,
        height: height,
      });
      myChart.setOption(option);
    },
    initChinaMap(myChart) {
      const geoData = this.mapData.map((item) => ({
        name: item.name,
        value: item.value,
      }));
      const option = {
        geo: {
          map: "china",
          roam: false, // 禁止拖拽
          zoom: 1.2, // 确保整体地图显示一致
          layoutCenter: ["40%", "50%"], // 保证地图在视口中心
          layoutSize: "100%", // 设置布局大小，使得地图填充整个视口
          itemStyle: {
            areaColor: "rgb(251, 251, 251)",
            borderColor: "darkgray",
            borderWidth: 1,
          },
          emphasis: {
            itemStyle: {
              areaColor: "rgba(88, 181, 192, 0.2)",
            },
            label: {
              color: "#000",
            },
          },
        },
        tooltip: {
          trigger: "item", // 这个是全局tooltip
          formatter: (params) => {
            console.log("TabLight", params);
            const { name } = params;
            const {
              预算目标,
              收款达成率,
              开单金额,
              开单达成率,
              账达成率,
              理论进度,
              成长率MTD,
              成长率YTD,
              E1E3级商圈达成率,
              E1E3级商圈达成率成长率MTD,
              E1E3级商圈成长率YTD,
            } = this.mapTipData[name];

            if (params.seriesType === "scatter") {
              // return `${params.name}: ${params.value[2]}`;
              return `
                <div>
                    <p  style="">预算目标:${this.formatNumberToThousandSeparator(
                      预算目标
                    )}人</p>
                    <p  style="">收款达成率:${收款达成率}人</p>
                    <p  style="">开单金额:${this.formatNumberToThousandSeparator(
                      开单金额
                    )}人</p>
                    <p  style="">开单达成率:${开单达成率}人</p>
                    <p  style="">账达成率:${账达成率}人</p>
                    <p  style="">理论进度:${理论进度}人</p>
                    <p  style="">成长率MTD:${成长率MTD}人</p>
                    <p  style="">成长率YTD:${成长率YTD}人</p>
                    <p  style="">E1E3级商圈达成率:${E1E3级商圈达成率}人</p>
                     <p>E1E3级商圈达成率成长率MTD:${E1E3级商圈达成率成长率MTD}人</p>
                     <p>E1E3级商圈成长率YTD:${E1E3级商圈成长率YTD}人</p>
              </div>
              `;
            }
            return ""; // 不显示省级地图的 tooltip
          },
        },
        series: [
          {
            type: "map",
            mapType: "china",
            roam: false,
            geoIndex: 0,
            itemStyle: {
              emphasis: { label: { show: false } },
            },
            data: this.mapData.map((item) => ({
              name: item.name,
              value: item.value,
              color: "#fffed7",
              datas: item.datas,
            })),
          },
          {
            name: "气泡",
            type: "scatter",
            coordinateSystem: "geo",
            symbol: "circle", // 设置符号为圆形
            symbolSize: function (val) {
              return Math.sqrt(val[2]) * 0.01; // 气泡大小与值的平方根成正比
            },

            roam: true,
            label: {
              normal: {
                formatter: (params) => {
                  if (params.value[2] > 2000000) {
                    return this.formatNumberToThousandSeparator(
                      params.value[2]
                    );
                  } else {
                    return "";
                  }
                },
                show: true,
                textStyle: {
                  color: "#000",
                  fontSize: 13,
                },
              },
              emphasis: {
                show: true,
              },
            },
            // tooltip: {
            //     // trigger: 'item',
            //     formatter: (p) => {
            //         return p.value[2]
            //     }
            // },
            itemStyle: {
              normal: {
                color: "#87CEEB", //标志颜色
              },
            },
            data: this.mapData.map((item) => {
              const province = echarts
                .getMap("china")
                .geoJson.features.find((f) => f.properties.name === item.name);
              if (province) {
                const value =
                  province.properties.centroid || province.properties.center;
                return {
                  name: item.name,
                  value: value.concat([item.value]), // 经度+纬度+气泡大小
                };
              }
            }),
            zlevel: 2, // 设置气泡的层级，确保气泡位于地图之上
            rippleEffect: {
              // 去除波纹效果
              show: false, // 禁止波纹效果
            },
          },
        ],
      };

      // 使用配置项设置图表
      myChart.setOption(option);
      window.onresize = function () {
        myChart.resize();
      };
    },
