import Vue from 'vue'
import App from './App.vue'

import './assets/common.less'

import ElementUI from 'element-ui'; // 2.1引入结构
import 'element-ui/lib/theme-chalk/index.css'; // 2.2引入样式

import dataV from '@jiaminghi/data-view'
import Router from 'vue-router'  //引入vue-router
import router from "./route/index"
import { formatNumberToThousandSeparator } from "./utils/index.js"

import { TitleComponent, TooltipComponent, LegendComponent, GeoComponent, ToolboxComponent, VisualMapComponent, GraphicComponent, PolarComponent } from 'echarts/components';
// 注意，新的接口中默认不再包含 Canvas 渲染器，需要显示引入，如果需要使用 SVG 渲染模式则使用 SVGRenderer
// 引入Canvas渲染器或SVG渲染器是必须的一步
import { CanvasRenderer } from 'echarts/renderers';
import { GaugeChart, MapChart, EffectScatterChart, PieChart, LinesChart, ScatterChart, SankeyChart, CustomChart, BarChart, PictorialBarChart, TreeChart, LineChart,TreemapChart } from 'echarts/charts';
import { use } from 'echarts/core';
import Axios from 'axios'
import scroll from 'vue-seamless-scroll'
Vue.use(scroll)
Axios.defaults.baseURL = '/api'
Axios.defaults.headers.post['Content-Type'] = 'application/json'
Vue.prototype.formatNumberToThousandSeparator = formatNumberToThousandSeparator;

Vue.use(ElementUI);
Vue.config.productionTip = false
use([GaugeChart, TitleComponent, TooltipComponent, CanvasRenderer, LegendComponent, GeoComponent, MapChart, EffectScatterChart, PieChart, LinesChart, ScatterChart, SankeyChart, CustomChart, BarChart, PictorialBarChart, ToolboxComponent, TreeChart, VisualMapComponent, LineChart, GraphicComponent, PolarComponent,TreemapChart])

Vue.use(dataV)

new Vue({
  router,
  render: h => h(App)
}).$mount('#app')
