<script>
import * as echarts from "echarts/core";

export default {
  name: "RightChart1",
  data() {
    return {};
  },
  mounted() {
    // this.initChart(echarts.init(this.$refs.right3Chart1), 80, "产品铺货达标率");
    // this.initChart(echarts.init(this.$refs.right3Chart2), 70, "第二陈列达标率");
    // this.initChart(echarts.init(this.$refs.right3Chart3), 65, "营业所1");
    // this.initChart(echarts.init(this.$refs.right3Chart4), 30, "营业所2");
    this.initBotChart(echarts.init(this.$refs.right3BotChart));
    this.setProgress(document.querySelectorAll(".circleRight3")[0], 23.6);
    this.setProgress(document.querySelectorAll(".circleRight3")[1], 52.8);
    this.setProgress(document.querySelectorAll(".circleRight3")[2], 33.3);
    this.setProgress(document.querySelectorAll(".circleRight3")[3], 45.6);
    this.setProgress(document.querySelectorAll(".circleRight3")[4], 23.6);
    this.setProgress(document.querySelectorAll(".circleRight3")[5], 52.8);
    this.setProgress(document.querySelectorAll(".circleRight3")[6], 33.3);
    this.setProgress(document.querySelectorAll(".circleRight3")[7], 45.6);
  },
  methods: {
    setProgress(element, progress) {
      const circle = element.querySelector(".progress");
      const radius = circle.r.baseVal.value;
      const circumference = 2 * Math.PI * radius;
      const offset = circumference - (progress / 100) * circumference;
      circle.style.strokeDashoffset = offset;
    },
    initChart(myChart, value, text) {
      var colorList = ["rgb(233, 54, 112)", "rgb(12, 26, 82)"];
      const option = {
        title: {
          text: value + "%",
          x: "40%",
          y: "center",

          textStyle: {
            fontSize: 14,
            color: "rgba(255, 255, 255, 0.8)",
          },
        },
        tooltip: {
          trigger: "item",
          show: false,
        },
        graphic: {
          elements: [
            {
              type: "text",
              left: "22%",
              top: "80%", // 根据需要调整文本位置
              style: {
                text: text,
                textAlign: "center",
                font: "14px Arial",
                fill: "rgba(255, 255, 255, 0.8)",
              },
            },
          ],
        },
        series: [
          {
            type: "pie",
            center: ["50%", "50%"],
            radius: ["50%", "75%"],
            clockwise: false,
            avoidLabelOverlap: false,
            hoverOffset: 0,
            itemStyle: {
              normal: {
                color: function (params) {
                  return colorList[params.dataIndex];
                },
              },
            },
            label: {
              show: false,
            },
            labelLine: {
              normal: {
                length: 20,
                length2: 30,
                lineStyle: {
                  width: 1,
                },
              },
            },
            data: [
              {
                name: "已完成",
                value: value,
              },
              {
                name: "未完成",
                value: 100 - value,
              },
            ],
          },
        ],
      };
      myChart.setOption(option);
    },
    initBotChart(myChart) {
      var data = [100, 90, 80, 70, 65, 60, 55, 50, 45, 40];
      var className = [
        "1 营业所1",
        "2 营业所2",
        "3 营业所3",
        "4 营业所4",
        "5 营业所5",
        "6 营业所6",
        "7 营业所7",
        "8 营业所8",
        "9 营业所9",
        "10 营业所10",
      ];
      const option = {
        title: {
          text: "得分/全国排名", // 设置标题文本
          left: "10%", // 设置标题位置为左对齐
          top: "10%", // 设置标题位置为顶部对齐
          textStyle: {
            fontSize: 16, // 设置标题字体大小
            fontWeight: "bold", // 设置标题字体粗细
            color: "#fff", // 设置标题颜色
          },
        },
        grid: {
          left: "5%",
          right: "5%",
          bottom: "5%",
          top: "15%",
          containLabel: true,
        },
        tooltip: {
          trigger: "axis",
          axisPointer: {
            type: "none",
          },
          formatter: function (params) {
            return params[0].name + ": " + params[0].value;
          },
        },
        backgroundColor: "",
        xAxis: {
          show: false,
          type: "value",
        },
        yAxis: [
          {
            type: "category",
            inverse: true,
            axisLabel: {
              show: true,
              textStyle: {
                color: "#fff",
              },
            },
            splitLine: {
              show: false,
            },
            axisTick: {
              show: false,
            },
            axisLine: {
              show: false,
            },
            data: className,
          },
          {
            type: "category",
            inverse: true,
            axisTick: "none",
            axisLine: "none",
            show: true,
            axisLabel: {
              textStyle: {
                color: "#ffffff",
                fontSize: "12",
              },
              formatter: function (value) {
                return value;
              },
            },
            data: data,
          },
        ],
        series: [
          {
            type: "bar",
            zlevel: 1,
            itemStyle: {
              normal: {
                barBorderRadius: 0,
                color: new echarts.graphic.LinearGradient(0, 0, 1, 0, [
                  { offset: 0, color: "#E93670" }, // 渐变起始颜色
                  { offset: 1, color: "#4894FF" }, // 渐变结束颜色
                ]),
              },
            },
            barWidth: 10,
            data: data,
          },
          {
            name: "背景",
            type: "bar",
            barWidth: 10,
            barGap: "-100%",
            data: [100, 100, 100, 100, 100, 100, 100, 100, 100, 100, 100, 100],
            itemStyle: {
              normal: {
                color: "#1B375E",
                barBorderRadius: 0,
              },
            },
          },
        ],
      };
      myChart.setOption(option);
    },
  },
};
</script>

