<template>
  <div class="top-right">
    <dv-border-box-12>
      <div class="title">铺货追踪</div>

      <!-- <dv-charts
        class="lc2-chart"
        :option="option1"
        style="width: 100%; height: 80%"
      /> -->
      <el-table
        :data="tableData"
        style="width: 90%; margin: 0 auto; margin-top: 100px"
      >
        <el-table-column prop="name" label="铺货名称" width="100">
        </el-table-column>
        <el-table-column prop="fulfillmentRate" label="铺货率" width="100">
          <template slot-scope="scope">
            {{
              ((scope.row.value / scope.row.targetValue) * 100).toFixed(2) + "%"
            }}
          </template>
        </el-table-column>
        <el-table-column prop="chain" label="环比" width="80">
        </el-table-column>
        <el-table-column prop="targetValue" label="目标铺货点数/已铺货点数">
          <template slot-scope="scope">
            <div class="left-progress">
              <el-tooltip
                class="item"
                effect="light"
                :content="'已铺货点数:' + scope.row.value"
                placement="top"
                v-if="scope.row.value - scope.row.targetValue < 0"
              >
                <div
                  :style="{ width: scope.row.value / 10 + 'px' }"
                  class="not-reached-proce"
                ></div>
              </el-tooltip>
              <el-tooltip
                class="item"
                effect="light"
                :content="'已铺货点数:' + scope.row.value"
                placement="top"
                v-else
              >
                <div
                  :style="{ width: scope.row.value / 10 + 'px' }"
                  class="progress-proce"
                ></div>
              </el-tooltip>
              <el-tooltip
                class="item"
                effect="light"
                :content="'目标铺货点数:' + scope.row.targetValue"
                placement="top"
              >
                <div
                  :style="{
                    marginLeft: scope.row.targetValue / 10 + 'px',
                  }"
                  class="target-proce"
                ></div>
              </el-tooltip>
            </div>
          </template>
        </el-table-column>
      </el-table>
    </dv-border-box-12>
  </div>
</template>

<script>
export default {
  name: "TopRight",
  data() {
    return {
      value1: 1,
      value2: 2,
      options: [
        { id: 1, label: "总公司" },
        { id: 2, label: "部门" },
        { id: 3, label: "测试111" },
      ],
      option1: {
        grid: {
          top: "",
          right: "10%",
          left: "6%",
          bottom: "30%",
        },
        series: [
          {
            type: "gauge",
            startAngle: -Math.PI / 2,
            endAngle: Math.PI * 1.5,
            arcLineWidth: 10,
            data: [
              {
                name: "目标铺货点数",
                value: 25,
                gradient: ["#03c2fd", "#DC143C", "#DC143C"],
              },
              {
                name: "已铺货点数",
                value: 45,
                gradient: ["#03c2fd", "#DC143C", "#DC143C"],
                radius: "53%",
              },
              {
                name: "铺货率",
                value: 65,
                gradient: ["#03c2fd", "#DC143C", "#DC143C"],
                radius: "46%",
              },
              {
                name: "环比",
                value: 65,
                gradient: ["#03c2fd", "#DC143C", "#DC143C"],
                radius: "39%",
              },
            ],
            axisLabel: {
              show: false,
            },
            axisTick: {
              show: false,
            },
            pointer: {
              show: false,
            },
            dataItemStyle: {
              lineCap: "round",
            },
            backgroundArc: {
              show: false,
            },
            details: {
              show: true,
              formatter: "{name}{value}%",
              position: "start",
              offset: [-10, 0],
              style: {
                fill: "#1ed3e5",
                fontSize: 13,
                textAlign: "right",
              },
            },
          },
        ],
      },
      tableData: [
        {
          name: "百事规格别",
          fulfillmentRate: "",
          chain: "54%",
          value: 1300,
          targetValue: 1000,
        },
        {
          name: "无糖规格别",
          fulfillmentRate: "",
          chain: "77%",
          value: 1200,
          targetValue: 1500,
        },
        {
          name: "百事规格别",
          fulfillmentRate: "",
          chain: "32%",
          value: 1100,
          targetValue: 900,
        },
        {
          name: "百事规格别",
          fulfillmentRate: "",
          chain: "45%",
          value: 1600,
          targetValue: 1300,
        },
      ],
    };
  },
};
</script>

<style scoped lang="less">
.top-right {
  width: 23%;
  height: 100%;
  display: flex;
  flex-direction: column;

  .lc2-header {
    height: 20px;
    line-height: 20px;
    font-size: 16px;
    text-indent: 20px;
    margin-top: 10px;
  }

  .lc2-details {
    height: 40px;
    font-size: 16px;
    display: flex;
    align-items: center;
    text-indent: 20px;

    span {
      color: #096dd9;
      font-weight: bold;
      font-size: 35px;
      margin-left: 20px;
    }
  }

  .lc2-chart {
    height: calc(~"100% - 80px");
  }
}

.title {
  height: 40px;
  font-size: 30px;
  text-indent: 20px;
  text-align: center;
  color: white;
  margin: 0;
  padding-top: 20px;

  margin-top: 25px;
  display: flex;
  justify-content: center;
}
/*最外层透明*/
::v-deep .el-table,
::v-deep .el-table__expanded-cell {
  background-color: transparent;
}
/* 表格内背景颜色 */
::v-deep .el-table th,
::v-deep .el-table tr,
::v-deep .el-table td {
  background-color: transparent !important;
  color: white;
}

// 鼠标经过颜色改为粉红色
::v-deep .el-table__body tr:hover > td {
  background-color: rgba(230, 230, 250, 0.3) !important;
}
/* 删除表格下最底层的横线 */
::v-deep .el-table::before {
  left: 0;
  bottom: 0;
  width: 100%;
  height: 0px;
}
.target-proce {
  width: 2px;
  height: 20px;
  background-color: white;
  margin: 0 auto;
  position: absolute;
  z-index: 100;
  bottom: 15px;
}
.left-progress {
  width: 100%;
  .progress-proce {
    height: 20px;
    background: linear-gradient(to right, #347ce9 0%, #28c3db 100%);
    position: absolute;
    bottom: 15px;
  }
  .not-reached-proce {
    height: 20px;
    background-color: rgb(203, 217, 243);
    position: absolute;
    bottom: 15px;
  }
}
</style>
