    this.setProgress(document.querySelectorAll(".circle2")[2], 33.3);
    this.tableData = this.tableDataBU;
  },
  methods: {
    setProgress(element, progress) {
      const circle = element.querySelector(".progress");
      const radius = circle.r.baseVal.value;
      const circumference = 2 * Math.PI * radius;
      const offset = circumference - (progress / 100) * circumference;
      circle.style.strokeDashoffset = offset;
    },
    rowClick() {
      if (this.isBU) {
        this.isSUO = true;
        this.isBU = false;
        this.isYEDAI = false;
        this.tableData = this.tableDataSUO;
        return;
      }
      if (this.isSUO) {
        this.tableData = this.tableDataYEDAI;
        this.isBU = false;
        this.isYEDAI = true;
        this.isSUO = false;
      }
    },
    clickBack() {
      this.tableData = this.tableDataBU;
      this.isSUO = false;
      this.isBU = true;
      this.isYEDAI = false;
    },
    renderHeader(h, { column, $index }) {
