<template>
  <div class="top-center">
    <div class="cc-header">
      <div>生产力追踪</div>
    </div>
    <!-- <div style="width: 100%; height: 90%" ref="scatterChart"></div> -->
    <div class="content">
      <dv-border-box-8
        class="content-top-item"
        v-for="item in heardData"
        :key="item.title"
      >
        <h3>{{ item.title }}</h3>
        <p>{{ item.value }}</p>
      </dv-border-box-8>
    </div>
    <div class="cotent-chart">
      <div style="width: 100%; height: 100%" ref="chart1"></div>
      <div style="width: 100%; height: 100%" ref="chart2"></div>
      <div style="width: 100%; height: 100%" ref="chart3"></div>
      <div style="width: 100%; height: 100%" ref="chart4"></div>
      <div style="width: 100%; height: 100%" ref="chart5"></div>
    </div>
  </div>
</template>

<script>
import LabelTag from "./LabelTag";
import * as echarts from "echarts/core";
import axios from "axios";

export default {
  name: "CenterCmp",
  components: {
    LabelTag,
  },
  mounted() {
    this.initChart(echarts.init(this.$refs.chart1), {
      value: 54,
      name: "达成进度",
      nameRight: "35%",
    });
    this.initChart(echarts.init(this.$refs.chart2), {
      value: 30,
      name: "日均生产力环比",
      nameRight: "27%",
    });
    this.initChart(echarts.init(this.$refs.chart3), {
      value: 87,
      name: "平均订单销量环比",
      nameRight: "25%",
    });
    this.initChart(echarts.init(this.$refs.chart4), {
      value: 36,
      name: "SKU Today Chain",
      nameRight: "25%",
    });
    this.initChart(echarts.init(this.$refs.chart5), {
      value: 36,
      name: "SKU MTD Chain",
      nameRight: "25%",
    });
  },
  data() {
    return {
      heardData: [
        { title: "生产力目标", value: 1232413 },
        { title: "日均生产力", value: 1542 },
        { title: "平均订单销量", value: 1200 },
        { title: "SKU Today", value: 12447 },
        { title: "SKU MTD", value: 138713 },
      ],
    };
  },
  methods: {
    initScatter(myChart) {
      var data = [
        {
          name: "生产力目标",
          value: "12355",
        },
        {
          name: "达成进度",
          value: "12093",
        },
        {
          name: "日均生产力",
          value: "8874",
        },
        {
          name: "平均订单销量",
          value: "7427",
        },
        {
          name: "SKU",
          value: "4308",
        },
      ];
      //偏移量
      var offsetData = [
        [80, 53],
        [35, 73],
        [30, 33],
        [60, 33],
        [10, 48],
      ];
      //symbolSize 散点气泡大小
      var symbolSizeData = [200, 185, 175, 165, 135];
      //
      //循环定义series的data值
      var datas = [];
      for (var i = 0; i < data.length; i++) {
        var item = data[i];

        datas.push({
          name: item.name + "\n" + item.value,
          value: offsetData[i],
          symbolSize: symbolSizeData[i],
          label: {
            normal: {
              textStyle: {
                fontSize: 24,
              },
            },
          },
          itemStyle: {
            normal: {
              color: new echarts.graphic.RadialGradient(0.3, 0.5, 0.7, [
                {
                  offset: 0,
                  color: "rgba(31,102,162,0)",
                },
                {
                  offset: 1,
                  color: "rgba(31,102,192,0.6)",
                },
              ]),
              opacity: 0.8,
              shadowColor: "#2e78eca8",
              shadowBlur: 10,
              shadowOffsetX: 1,
              shadowOffsetY: 1,
            },
          },
        });
      }

      const option = {
        backgroundColor: "",
        grid: {
          show: false,
          top: 10,
          bottom: 10,
        },
        xAxis: {
          type: "value",
          show: false,
          min: 0,
          max: 100,
          nameLocation: "middle",
          nameGap: 5,
        },
        yAxis: {
          min: 0,
          show: false,
          max: 100,
          nameLocation: "middle",
          nameGap: 30,
        },

        series: [
          {
            type: "scatter",
            symbol: "circle",
            symbolSize: 120,
            label: {
              normal: {
                show: true,
                formatter: "{b}",
                color: "#fff",
                textStyle: {
                  fontSize: "20",
                },
              },
            },
            itemStyle: {
              normal: {
                color: "#00acea",
              },
            },
            data: datas,
          },
        ],
      };
      myChart.setOption(option);
    },
    initChart(myChart, data) {
      const { value, name, nameRight } = data;
      var ancost = (parseInt(200) * 100) / parseInt(600);
      var data = [
        {
          value: value,
          name: "",
        },
        {
          value: 50,
          name: "",
        },
      ];
      //这里才是关键整去掉下半部分的关键，
      //计算data中value的总和
      var a = 0;
      for (var i = 0; i < data.length; i++) {
        a += data[i].value;
      }
      console.log("aaaa", a);
      //添加新的元素到data中，并设置其颜色样式为白色
      data.push({
        value: a,
        name: "__other",
        itemStyle: {
          normal: {
            color: "rgba(0,0,0,0)",
          },
        },
      });

      const option = {
        title: [
          {
            text: value + "%",
            right: "40%",
            bottom: "10%",
            textStyle: {
              color: "#FFF",
              fontStyle: "normal",
              fontWeight: "400",
              fontFamily: "Times New Roman",
              fontSize: 13,
            },
          },
          {
            text: "",
            left: "5%",
            bottom: "25%",
            textStyle: {
              color: "#fff",
              fontStyle: "normal",
              fontWeight: "400",
              fontFamily: "sans-serif",
              fontSize: 13,
            },
          },
          {
            text: "",
            right: "-2%",
            bottom: "25%",
            textStyle: {
              color: "#fff",
              fontStyle: "normal",
              fontWeight: "400",
              fontFamily: "sans-serif",
              fontSize: 13,
            },
          },
          {
            text: name,
            right: nameRight,
            bottom: "-5%",
            textStyle: {
              color: "#fff",
              fontStyle: "normal",
              fontWeight: "400",
              fontFamily: "Times New Roman",
              fontSize: 13,
            },
          },
        ],
        series: [
          {
            // name:'访问来源',
            type: "pie",
            hoverAnimation: false,
            radius: ["100%", "150%"],
            center: ["50%", "80%"],
            startAngle: 180,
            // avoidLabelOverlap: false,
            label: {
              normal: {
                show: false,
                position: "center",
              },

              emphasis: {
                show: false,
                textStyle: {},
              },
            },
            color: ["#33a8ff", "#8dd4ff"],
            data: data,
          },
        ],
      };
      myChart.setOption(option, true);
    },
  },
};
</script>

<style scoped lang="less">
.top-center {
  width: 100%;
  height: 100%;
  margin: 0px;
  padding: 0px;
  display: flex;
  flex-direction: column;

  .cc-header {
    height: 40px;
    font-size: 30px;
    text-indent: 20px;
    text-align: center;
    color: white;
    margin: 0;
    padding-top: 30px;
    display: flex;
    justify-content: center;
  }

  .content {
    width: 95%;
    margin: 0 auto;
    display: flex;
    justify-content: center;
    margin-top: 70px;
    .content-top-item {
      width: 20%;
      height: 100px;
      text-align: center;
      margin: 0 10px;
      h3 {
        margin-bottom: 0;
      }
    }
  }
  .cotent-chart {
    width: 95%;
    margin: 0 auto;
    margin-top: 100px;
    display: flex;
    justify-content: center;
    div {
      width: 20%;
      height: 200px;
      margin: 0 10px;
    }
  }

  .cc-header div {
    margin: 0 auto;
  }
  .wrap {
    width: 100%;
    // background-color: #191c26;
    margin-top: 30px;
  }

  .dark-select {
    display: flex;
    justify-content: center;

    // 修改input默认值颜色 兼容其它主流浏览器
    /deep/ input::-webkit-input-placeholder {
      color: rgba(255, 255, 255, 0.5);
    }

    /deep/ input::-moz-input-placeholder {
      color: rgba(255, 255, 255, 0.5);
    }

    /deep/ input::-ms-input-placeholder {
      color: rgba(255, 255, 255, 0.5);
    }

    // input框
    /deep/ .el-select,
    /deep/ .el-input,
    /deep/ .el-input__inner {
      background-color: rgba(255, 255, 255, 0.04);
      color: rgba(255, 255, 255, 0.5);
      border: none; // 去掉边框
      // border-color: #XXXXXX // 默认边框的颜色
      text-align: left;
      border-radius: 4px;
    }

    // 选中时边框颜色
    // /deep/ .el-input__inner:focus{
    //     border-color: #XXXXXX;
    // }

    // 鼠标悬浮时 input框颜色
    /deep/ .el-input__inner:hover {
      background-color: rgba(255, 255, 255, 0.12);
    }

    // input框 右侧的箭头
    /deep/ .el-select .el-input .el-select__caret {
      color: rgba(255, 255, 255, 0.5);
    }

    // option选项 上面的箭头
    /deep/ .el-popper[x-placement^="bottom"] .popper__arrow::after {
      border-bottom-color: rgba(43, 45, 55, 0.8);
      z-index: 9999;
    }

    /deep/ .popper__arrow {
      border: none;
    }

    // option选项 最外层
    /deep/ .el-select-dropdown {
      border: none !important;
      background: rgba(43, 45, 55, 0.8) !important;
      z-index: 9999;
    }

    // option选项 文字样式
    /deep/ .el-select-dropdown__item {
      color: rgba(255, 255, 255, 0.5) !important;
      z-index: 9999;
    }

    /deep/ .el-select-dropdown__item.selected span {
      color: rgba(255, 255, 255, 0.8) !important;
      z-index: 9999;
    }

    // 移入option选项 样式调整
    /deep/ .el-select-dropdown__item.hover {
      background-color: rgba(255, 255, 255, 0.06);
      color: rgba(255, 255, 255, 0.8) !important;
      z-index: 9999;
    }

    // 下拉框垂直滚动条宽度
    /deep/ .el-scrollbar__bar.is-vertical {
      width: 10px;
      top: 2px;
    }

    // 下拉框最大高度
    /deep/ .el-select-dropdown__wrap {
      max-height: 200px;
    }

    /deep/ .dv-scroll-board .header {
      height: 20px;
    }
  }
}
</style>
