<template>
  <div id="tabLightIndex">
    <div class="leftNav">
      <el-menu
        @select="handleSelect"
        :default-active="menuActive || '/PerformanceProgress'"
        class="el-menu-vertical-demo"
      >
        <el-menu-item
          v-for="item in routers"
          :key="item.path"
          :index="item.path"
        >
          <span slot="title">{{ item.name }}</span>
        </el-menu-item>
      </el-menu>
    </div>
    <div class="rightContainer">
      <div class="rightHeader">
        <h1>GM日常系统 / {{ headerTitle }}</h1>
      </div>
      <div class="rightMain">
        <router-view></router-view>
      </div>
    </div>
  </div>
</template>
<script>
export default {
  name: "Index",
  components: {},
  mounted() {},
  computed: {
    menuActive() {
      return this.$route.path;
    },
    headerTitle() {
      return this.routers.filter((item) => item.path === this.$route.path)[0]
        .name;
    },
  },
  data() {
    return {
      routers: [
        { path: "/PerformanceProgress", name: "业绩进度" },
        // { path: "/Tongluli", name: "通路力追踪" },
        // { path: "/Qidaoli", name: "渠道力追踪" },
        // { path: "/Bingdongli", name: "冰冻力追踪" },
        { path: "/Shengchanli", name: "生产力追踪" },
        // { path: "/Pinpai", name: "品牌管理" },
        // { path: "/Guige", name: "规格管理" },
        // { path: "/WIEzhixing", name: "WIE执行" },
      ],
    };
  },
  methods: {
    handleSelect(path) {
      this.$router.push(path);
    },
  },
};
</script>
<style lang="less">
#tabLightIndex {
  width: 100%;
  height: auto;
  background-color: rgb(245, 245, 245);
  display: flex;
  min-height: 100%;

  .leftNav {
    width: 180px;

    .el-menu-vertical-demo {
      width: 180px;
      height: 100%;
    }
  }

  .rightContainer {
    flex-grow: 1;
    display: flex;
    flex-direction: column;

    .rightHeader {
      width: 100%;
      height: 100px;
      text-align: left;
      line-height: 100px;
      margin-left: 10px;

      h1 {
        color: rgb(102, 102, 102);
        margin: 0;
        padding: 0;
        transform: translateX(20px);
        /* 让文字右对齐 */
      }
    }

    .rightMain {
      width: 100%;
      flex-grow: 1;
    }
  }
}
</style>
