<script>
export default {
  name: "LeftChart2",
  data() {
    return {};
  },
  mounted() {
    this.setProgress(document.querySelectorAll(".circle2")[0], 23.6);
    this.setProgress(document.querySelectorAll(".circle2")[1], 52.8);
    this.setProgress(document.querySelectorAll(".circle2")[2], 33.3);
  },
  methods: {
    setProgress(element, progress) {
      const circle = element.querySelector(".progress");
      const radius = circle.r.baseVal.value;
      const circumference = 2 * Math.PI * radius;
      const offset = circumference - (progress / 100) * circumference;
      circle.style.strokeDashoffset = offset;
    },
  },
};
</script>

