<template>
  <div class="top-right">
    <dv-border-box-12>
      <div class="title">付费管理</div>
      <div class="main-chart">
        <div style="width: 100%; height: 200px" ref="topChart"></div>
        <div
          style="width: 100%; height: 80px; positon: relative; bottom: 40px"
          ref="topChart2"
        ></div>
        <div style="width: 100%; height: 30%" ref="bottomChart"></div>
      </div>
    </dv-border-box-12>
  </div>
</template>

<script>
import * as echarts from "echarts/core";

export default {
  name: "TopRight",
  mounted() {
    this.initChart(
      echarts.init(this.$refs.topChart),
      [1236, 1129, 925],
      ["付费客户数", "上报家数", "平均拜访次数"],
      "付费客户",
      1300
    );
    this.initChart2(
      echarts.init(this.$refs.topChart2),
      [80],
      ["付费占比"],
      "",
      100
    );
