      //   var chartName = [
      //     "柜台01",
      //     "柜台02",
      //     "柜台03",
      //     "柜台04",
      //     "柜台05",
      //     "柜台06",
      //     "柜台07",
      //     "柜台08",
      //     "柜台09",
      //     "柜台10",
      //   ];

      // 	var max = Math.max.apply(null,chartData);
      var emptyData = chartData.map(function (v, i) {
        var item = {
          value: baseValue,
          label: {
            //右边数值
            formatter: "{a|" + v + "}",
            position: "right",
            rich: {
              a: {
                color: "#fff",
                fontSize: 13,
                padding: [5, 10],
              },
            },
          },
        };
        return item;
      });

      const option = {
        title: {
          text: titleName,
          textStyle: {
            color: "#fff",
          },
        },
        backgroundColor: "",
        tooltip: {
          show: false,
          trigger: "item",
          formatter: function (params, ticket, callback) {
            var res = "";
            var name = chartName[params.dataIndex];
            var value = chartData[params.dataIndex];
            res = name + ":" + value;
            return res;
          },
        },
        grid: {
          top: "10%",
          right: "10%",
          left: "10%",
          bottom: "10%",
          containLabel: true,
        },
        xAxis: [
          {
            show: false,
          },
          {
            type: "value",
            axisLabel: {
              //坐标轴字体颜色
              show: false,
            },
            axisLine: {
              show: false,
              lineStyle: {
                color: "rgba(255,255,255,0.12)",
              },
            },
            axisTick: {
              //y轴刻度线
              show: false,
            },
            splitLine: {
              //网格
              show: false,
            },
            max: function (value) {
              return value.max;
            },
          },
        ],
