            type: "category",
            name: "",
            axisTick: {
              show: false,
            },
            splitLine: {
              show: false,
            },
            axisLabel: {
              show: false,
            },
            axisLine: {
              show: false,
            },
            inverse: true,
            data: chartName,
          },
        ],
        series: [
          {
            type: "bar",
            barWidth: "30%",
            yAxisIndex: 1,
            silent: true,
            itemStyle: {
              normal: {
                color: "#999999",
                barBorderRadius: [20, 20],
              },
            },
            label: {
              show: true,
            },
            data: emptyData,
          },
