            show: true,
            type: "bar",
            barWidth: "35%",
            z: 2,
            label: {
              show: false,
            },
            itemStyle: {
              normal: {
                color: new echarts.graphic.LinearGradient(
                  0,
                  0,
                  1,
                  1,
                  [
                    {
                      offset: 0,
                      color: "#347ce9", // 0% 处的颜色
                    },
                    {
                      offset: 1,
                      color: "#28c3db", // 100% 处的颜色
                    },
                  ],
                  false
                ),
                barBorderRadius: [20, 20],
              },
            },
            data: chartData,
          },
        ],
      };
      myChart.setOption(option);
    },
