          formatter: function (params, ticket, callback) {
            var res = "";
            var name = chartName[params.dataIndex];
            var value = chartData[params.dataIndex];
            res = name + ":" + value;
            return res;
          },
        },
        grid: {
          top: "",
          right: "10%",
          left: "15%",
          bottom: "10%",
          containLabel: true,
