            type: "category",
            name: "",
            axisTick: {
              show: false,
            },
            splitLine: {
              show: false,
            },
            axisLabel: {
              show: false,
            },
            axisLine: {
              show: false,
            },
            inverse: true,
            data: chartName,
          },
        ],
        series: [
          {
            type: "bar",
            barWidth: "30%",
            yAxisIndex: 1,
            silent: true,
            itemStyle: {
              normal: {
                color: "#999999",
                barBorderRadius: [20, 20],
              },
            },
            label: {
              show: true,
            },
            data: emptyData,
          },
          {
            show: true,
            type: "bar",
            barWidth: "35%",
            z: 2,
            label: {
              show: false,
            },
            itemStyle: {
              normal: {
                color: new echarts.graphic.LinearGradient(
                  0,
                  0,
                  1,
                  1,
                  [
                    {
                      offset: 0,
                      color: "#347ce9", // 0% 处的颜色
                    },
                    {
                      offset: 1,
                      color: "#28c3db", // 100% 处的颜色
                    },
                  ],
                  false
                ),
                barBorderRadius: [20, 20],
              },
            },
            data: chartData,
          },
        ],
      };
      myChart.setOption(option);
    },
  },
};
</script>

<style scoped lang="less">
.top-right {
  width: 23%;
  height: 100%;
  display: flex;
  flex-direction: column;
  .title {
    height: 40px;
    font-size: 30px;
    text-indent: 20px;
    text-align: center;
    color: white;
    margin: 0;
    padding: 30px 0;
    display: flex;
    justify-content: center;
  }
  .main-chart {
    width: 100%;
    height: 90%;
  }
}
</style>
