<script>
export default {
  name: "RightChart1",
  mounted() {
    this.setProgress(document.querySelectorAll(".circleRight2")[0], 23.6);
    this.setProgress(document.querySelectorAll(".circleRight2")[1], 52.8);
    this.setProgress(document.querySelectorAll(".circleRight2")[2], 33.3);
    this.setProgress(document.querySelectorAll(".circleRight2")[3], 45.6);
    this.setProgress(document.querySelectorAll(".circleRight2")[4], 23.6);
    this.setProgress(document.querySelectorAll(".circleRight2")[5], 52.8);
    this.setProgress(document.querySelectorAll(".circleRight2")[6], 33.3);
    this.setProgress(document.querySelectorAll(".circleRight2")[7], 45.6);
  },
  data() {
    return {};
  },
  methods: {
    setProgress(element, progress) {
      const circle = element.querySelector(".progress");
      const radius = circle.r.baseVal.value;
      const circumference = 2 * Math.PI * radius;
      const offset = circumference - (progress / 100) * circumference;
      circle.style.strokeDashoffset = offset;
    },
  },
};
