<template>
  <div class="top-left">
    <dv-border-box-12>
      <div class="tiele">客户管理</div>
      <div class="mainBar">
        <div style="width: 50%; height: 50%" ref="numBar"></div>
        <div style="width: 50%; height: 50%" ref="proportionBar1"></div>
        <div style="width: 50%; height: 50%" ref="proportionBar2"></div>
        <div style="width: 50%; height: 50%" ref="proportionBar3"></div>
      </div>
    </dv-border-box-12>
  </div>
</template>

<script>
import * as echarts from "echarts/core";

export default {
  name: "TopLeft",
  data() {
    return {};
  },
  mounted() {
    this.initNumBarChart(echarts.init(this.$refs.numBar));
    this.initProportionBar(echarts.init(this.$refs.proportionBar1), {
      value: 30,
      name: "排线率",
    });
    this.initProportionBar(echarts.init(this.$refs.proportionBar2), {
      value: 50,
      name: "排线拜访率",
    });
    this.initProportionBar(echarts.init(this.$refs.proportionBar3), {
      value: 80,
      name: "成交率",
    });
  },
  methods: {
    initNumBarChart(myChart) {
      const nameArr = ["客户数", "新增", "成交", "未成交"];
      const valueArr = [
        { value: 300, chValue: "50%" },
        { value: 60, chValue: "50%" },
        { value: 80, chValue: "50%" },
        { value: 220, chValue: "50%" },
      ];
      const option = {
        grid: {
          top: "10%",
          left: "12%",
          right: "5%",
          bottom: "20%",
        },
        tooltip: {
          trigger: "axis",
          backgroundColor: "#3A4667",
          borderColor: "#3A4667",
          textStyle: {
            color: "#fff",
          },
          formatter: (p) => {
            console.log("ppppppp", p);
            const { name, value } = p[0];
            return `<div>${name}: ${value}<div><div>环比: ${p[0].data.chValue}<div>`;
          },
          axisPointer: {
            type: "cross",
            crossStyle: {
              color: "#fff",
            },
          },
        },
        legend: {
          icon: "rect",
          top: 10,
          right: 5,
          itemWidth: 10,
          itemHeight: 10,
          textStyle: {
            fontSize: 12, // 字体大小
            // 			color: '#B3CFFF', // 字体颜色
            color: "#fff",
          },
        },
        xAxis: {
          type: "category",
          axisPointer: {
            type: "shadow",
          },
          axisLine: {
            lineStyle: {
              color: "rgba(112, 138, 198, 1)",
            },
          },
          axisLabel: {
            textStyle: {
              color: "#fff", // x轴文本颜色
              fontSize: 12,
            },
            // rotate:30,
          },
          axisTick: {
            show: false,
          },
          data: nameArr,
        },
        yAxis: {
          type: "value",
          splitLine: {
            show: true,
            lineStyle: {
              color: "#ffff",
              type: "dashed",
            },
          },
          axisLabel: {
            formatter: "{value}",
            textStyle: {
              color: "#ffff", // y轴文本颜色
              fontSize: 12,
            },
          },
          name: "",
          nameTextStyle: {
            color: "#B3CFFF",
            fontSize: 12,
          },
        },
        series: [
          {
            type: "bar",
            name: "",
            barWidth: 27,
            emphasis: {
              itemStyle: {
                color: "#7fb7e9",
              },
            },
            itemStyle: {
              // 使用方法二的写法
              color: {
                type: "linear",
                x: 0, //右
                y: 0, //下
                x2: 0, //左
                y2: 1, //上
                colorStops: [
                  {
                    offset: 0,
                    color: "#347ce9", // 0% 处的颜色
                  },
                  {
                    offset: 1,
                    color: "#28c3db", // 100% 处的颜色
                  },
                ],
              },
            },
            // itemStyle: {
            //   normal: {
            //     color: "#2A82E4",
            //   },
            // },
            data: valueArr,
          },
        ],
      };

      myChart.setOption(option);
    },
    initProportionBar(myChart, data) {
      console.log("data", data);
      const { value, name } = data;
      console.log("value", value);
      let max = 100;
      const option = {
        backgroundColor: "",
        title: {
          text: name + " " + (value || "-") + "%" + "             ",
          x: "center",
          y: "center",
          textStyle: {
            color: "#fff",
            fontSize: "13",
            fontWeight: "600",
            marginRight: "50",
          },
        },
        angleAxis: {
          axisLine: {
            show: false,
          },
          axisLabel: {
            show: false,
          },
          splitLine: {
            show: false,
          },
          axisTick: {
            show: false,
          },
          min: 0,
          max: 100,
          startAngle: 90,
        },
        radiusAxis: {
          type: "category",
          axisLine: {
            show: false,
          },
          axisTick: {
            show: false,
          },
          axisLabel: {
            show: true,
          },
          data: [],
        },
        polar: {
          radius: "130%",
          center: ["50%", "50%"],
        },
        series: [
          // 极坐标系下柱状图
          // 实际值
          {
            type: "bar",
            data: [value],
            z: 1,
            coordinateSystem: "polar",
            barMaxWidth: 33,
            name: name,
            roundCap: 1,
            color: new echarts.graphic.LinearGradient(0, 1, 0, 0, [
              {
                offset: 0,
                color: "#0062d8",
              },
              {
                offset: 0.5,
                color: "#008cec",
              },
              {
                offset: 1,
                color: "#5bd9ff",
              },
            ]),
            // color: 'rgba(97,93,228,1)',
          },
          // 背景图形
          {
            type: "bar",
            data: [max],
            z: 0,
            silent: true,
            coordinateSystem: "polar",
            barMaxWidth: 33,
            roundCap: true,
            color: "#cee7f9",
            barGap: "-100%",
          },
        ],
      };
      myChart.setOption(option);
    },
  },
};
</script>

<style scoped lang="less">
.top-left {
  width: 22%;
  height: 100%;
  display: flex;
  flex-direction: column;
  .mainBar {
    width: 100%;
    height: 80%;
    display: flex;
    flex-wrap: wrap;
  }
}

.unit-label {
  display: none;
}

.tiele {
  height: 40px;
  font-size: 30px;
  text-indent: 20px;
  text-align: center;
  color: white;
  margin: 0;
  padding: 30px 0;
  display: flex;
  justify-content: center;
}
</style>
